/* Portfolio.css */

#portfolio h2 {
  margin: 100px 200px 0px;
}



.projects {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 10%;
  justify-content: center;
}

.project {
  margin: 20px;
  text-align: center;
}

.project img {
  width: 600px;
  height: 500px;
  object-fit: cover;
}

.project-title {
  margin-top: 10px;
  font-size: 25px;
  font-weight: bold;
  color: #fff;
}

.project-links {
  margin-top: 10px;
}

.project-links a {
  margin-right: 10px;
  color: #fff;
  text-decoration: none;
}

.project-links a:hover {
  text-decoration: underline;
}


